import CheckCircleOutlineOutlinedIcon from "@mui/icons-material/CheckCircleOutlineOutlined";

import { BarcodeEditableCell } from "./Cells/BarcodeEditableCell";
import { CheckCell } from "./Cells/CheckCell";
import { DefaultCell } from "./Cells/DefaultCell";
import { MarginCell } from "./Cells/MarginCell";
import { PriceCell } from "./Cells/PriceCell";
import { RemoveButton } from "./RemoveButton";

export const columns = [
  {
    headerName: "",
    field: "code",
    width: 140
  },
  {
    headerName: "",
    field: "name",
    flex: 1,
    editable: true,
    renderCell: (props) => <DefaultCell {...props} />
  },
  {
    headerName: " ",
    field: "purchasePrice",
    editable: true,
    type: "number",
    renderCell: ({ value }) => (value ? Number(value).toFixed(2) : ""),
    valueSetter: ({ value, row }) => ({
      ...row,
      purchasePrice: value,
      price: value && row.margin ? value + value * (row.margin / 100) : null
    })
  },
  {
    headerName: " (%)",
    field: "margin",
    editable: true,
    type: "number",
    valueSetter: ({ value, row }) => ({
      ...row,
      margin: value,
      price:
        value && row.purchasePrice
          ? row.purchasePrice + row.purchasePrice * (value / 100)
          : null
    }),
    renderCell: (props) => <MarginCell {...props} />
  },
  {
    headerName: " ",
    field: "price",
    editable: true,
    type: "number",
    width: 114,
    renderCell: (props) => <PriceCell {...props} />
  },
  {
    headerName: " ",
    field: "originalPrice",
    type: "number",
    width: 110,
    renderCell: ({ row }) =>
      row.original.price ? Number(row.original.price).toFixed(2) : "-"
  },
  {
    headerName: "",
    field: "is_weight",
    width: 74,
    renderCell: (props) => <CheckCell {...props} />
  },
  {
    headerName: "",
    field: "marked",
    width: 102,
    renderCell: (props) => <CheckCell {...props} />
  },
  {
    headerName: "-",
    field: "related_barcodes",
    editable: true,
    renderCell: (props) => <DefaultCell {...props} />,
    renderEditCell: (props) => <BarcodeEditableCell {...props} />
  },
  {
    headerName: "",
    field: "saved",
    width: 96,
    align: "center",
    renderCell: ({ row }) =>
      Boolean(
        row.original.id &&
          ["code", "name", "price", "is_weight", "related_barcodes"].every(
            (field) => row[field] === row.original[field]
          )
      ) && <CheckCircleOutlineOutlinedIcon sx={{ color: "success.main" }} />
  },
  {
    headerName: "",
    field: "actions",
    headerAlign: "center",
    width: 60,
    renderCell: (props) => <RemoveButton {...props} />
  }
];

columns.forEach((column) => {
  column.sortable ??= false;
  column.headerAlign ??= "left";
  column.align ??= "left";
});
